.image-display-card-container {
    width: clamp(12rem, 15vw, 18rem);
    height: clamp(12rem, 15vw, 18rem);
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.8), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.image-display-card-image {
    height: 100%;
}